import React from 'react';


const Enter = (props) => (

    <div>
        { props.name === undefined &&
        <div className="enter"> 
            <div className={"enter-"+props.mode}>
                {props.name === undefined && <h1 > Enter a Ticker </h1>  } 
            </div>
        </div>
        }
    </div>


)



export default Enter;