import React from 'react'
import Form from '../Components/Form'




const Navbar = (props) => (
    <nav className={"navbar"} id={'nav-'+props.mode}> 
        <header id="top" href="#top" className='header'> Stock Info  </header>
        <a className="icon">
            <img src="https://cdn0.iconfinder.com/data/icons/basic-ui-vol-1/32/UI_stroke-05-512.png" id={"icon-img-"+props.mode}></img>
        </a>

        <Form
            getTicker={props.getTicker} 
            mode={props.mode}
        />

        <div className='nav-links'>
            <a id="nav-link" href="#chart" > Chart </a>
            <a id="nav-link" href="#info" > Info </a>
            <a id="nav-link" href="#news" > News </a>
        </div>
    </nav>

)














export default Navbar;