import React from 'react';

import TradingViewWidget from 'react-tradingview-widget';

import { TradingViewEmbed, widgetType } from "react-tradingview-embed";

const StockChart = (props) => (

  <div id="chart" >
    {props.symbol &&
      <div className='chart-widget'>
        <TradingViewEmbed
          widgetType={widgetType.MARKET_OVERVIEW}
          widgetConfig={
          {
            "colorTheme": props.mode,
            "dateRange": "1d",
            "showChart": true,
            "locale": "en",
            "largeChartUrl": "",
            "isTransparent": true,
            "width": "100%",
            "height": "400",
            "plotLineColorGrowing": "rgba(87, 243, 144, 1)",
            "plotLineColorFalling": "rgb(229, 64, 18)",
            "gridLineColor": "rgba(233, 233, 234, 1)",
            "scaleFontColor": "rgba(120, 123, 134, 1)",
            "belowLineFillColorGrowing": "rgba(87, 243, 144, 0.12)",
            "belowLineFillColorFalling": "rgba(243, 15, 15, 0.12)",
            "symbolActiveColor": "rgba(33, 150, 243, 0.12)",
            "tabs": [
              {
                "title": "Stocks",
                "symbols": [
                  {
                    "s": props.symbol
                  }
                ],
              }
            ]
          }
        }
        />
      </div>
    } 
  </div>

);







